import React from 'react';
import {connect} from 'react-redux';
import {removeProductToCart,incrementCartQuantity,decrementCartQuantity} from "../../actions/cartActions";

function Cart({cartItems,incrementCartQuantity,decrementCartQuantity,removeProductToCart}) {

    let ListCart = [];
    let TotalCart=0;
    Object.keys(cartItems).forEach(function(item){
        TotalCart+=cartItems[item].count * cartItems[item].price;
        ListCart.push(cartItems[item]);
    });

    function TotalPrice(price,tonggia){
        return Number(price * tonggia).toLocaleString('en-US');
    }
    return (
        <div className='cart'>
            <div className='container'>
                <div className='main'>
                    <div className='cart-first'>
                        <div className='cart-first-item'>
                            <h2>Shoping cart ({cartItems.length})</h2>
                            <div className='msg-list'></div>
                            <div className='btn-all'></div>
                        </div>
                        <div className='cart-first-item'>
                            <div className='products'>
                                <ul>
                                    {cartItems.length == 0 ?
                                            <li>
                                                <h3>Cart is empty</h3>
                                                <p>No items in cart</p>
                                            </li>
                                        :
                                        ListCart.map((product,key) => {
                                            TotalPrice(product.price,product.count)
                                            return(
                                                <li>
                                                    <div className='product'>
                                                        <button className='delete' onClick={() => removeProductToCart(product)}><i class="far fa-trash-alt"></i></button>
                                                        <div className='image'>
                                                            <img src={product.image}/>
                                                        </div>
                                                        <div className='content'>
                                                            <div className='title'>
                                                                <p>{product.title}</p>
                                                            </div>
                                                            <div className='color'>
                                                                <p><b>Color</b>: Black</p>
                                                            </div>
                                                            <div className='price'>
                                                                <p><b>${product.price}</b></p>
                                                            </div>
                                                        </div>
                                                        <div className='options'>
                                                            <div className='buttons'>
                                                                <input type='submit' onClick={() => decrementCartQuantity(key)} value='-'/>
                                                                <p>{product.count}</p>
                                                                <input type='submit' onClick={() => incrementCartQuantity(key)} value='+'/>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </li>
                                            )})
                                    }
                                </ul>
                            </div>
                        </div>
                        <div className='cart-first-item'>
                            <div className="card-container ">
                                <div className="payment-cabinet">
                                    <h3>
                                        Payment methods
                                    </h3>
                                    <ul>
                                        <li><img src="//ae01.alicdn.com/kf/HTB1fNUEaNrvK1RjSsze761ObFXaX.png"/></li>
                                        <li><img src="//ae01.alicdn.com/kf/HTB1xDsCaODxK1Rjy1zc761GeXXae.png"/></li>
                                        <li><img src="//ae01.alicdn.com/kf/HTB1RS7DaOHrK1Rjy0Fl763saFXax.png"/></li>
                                        <li><img src="//ae01.alicdn.com/kf/HTB17y7yaIvrK1Rjy0Fe763TmVXaS.png"/></li>
                                        <li><img src="//ae01.alicdn.com/kf/HTB19ZUGaJzvK1RkSnfo762MwVXaw.png"/></li>
                                        <li><img src="//ae01.alicdn.com/kf/HTB1OzoFaOYrK1Rjy0Fd763CvVXaQ.png"/></li>
                                    </ul>
                                </div>
                                <div className="buyer-protection">
                                    <h3>
                                          Buyer Protection
                                    </h3>
                                    <p>
                                        You can claim your money back if you have not received the item or it does not match the description.
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='cart-sec'>
                        <div className='price'>
                            <h2>Summary</h2>
                            <div className='table-options'>
                                <p>Partial total</p>
                                <p>{Number(TotalCart).toLocaleString('en-US')} $</p>
                            </div>
                            <div className='table-options'>
                                <p>Shiping</p>
                                <p>US $ 0.00</p>
                            </div>
                            <div className='total-price'>
                                <div className='content'>
                                    <h4>Total</h4>
                                    <h4>
                                        {Number(TotalCart).toLocaleString('en-US')} $
                                    </h4>
                                </div>
                                <div className='button'>
                                    <button>BUY</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = state =>{
    return{
        cartItems:state.shop.items,
    }
}

export default connect(mapStateToProps,{removeProductToCart,incrementCartQuantity,decrementCartQuantity})(Cart);
