export const LOGIN = 'LOGIN';
export const LOGOUT = 'LOGOUT';
export const REGISTER = 'REGISTER';


export function Login(payload){
    return{
        type:LOGIN,
        payload
    }
}
export function Logout(payload){
    return{
        type:LOGOUT,
        payload
    }
}
export function Register(payload){
    return{
        type:REGISTER,
        payload
    }
}
