import {
    LOGIN,
    LOGOUT,
    REGISTER
} from '../actions/userActions';

const userReducer = (state = {user:null}, action ) => {
    switch (action.type) {
        case LOGIN:
            return {user:action.payload}
        case LOGOUT:
            return {user:null}
        case REGISTER:
            return {...state}
        default:
            return state;

    }
};

export default userReducer;