import React from 'react';
import ModalLogin from '../Modal/Modal'
import {connect} from 'react-redux';
import {Logout} from '../../../actions/userActions';

function Account({user,Logout}) {

    return (
        <div className="main">
            <div className='user'>
                {
                    user.user ? 
                        <><img src="https://ae01.alicdn.com/kf/Hf768b4fa794e44bfb7cc86e4a528a035h.png"/>
                        <h5>Welcome Arian</h5></>
                    :
                    <>
                        <img src="https://ae01.alicdn.com/kf/Hf768b4fa794e44bfb7cc86e4a528a035h.png"/>
                        <h5>Welcome to AliExpress</h5>
                    </>
                }
                
            </div>
            <div className='buttons'>
                {
                    user.user? 
                        <button onClick={() => Logout()}>Logut</button>
                    :
                        <>
                            <button>Join</button>
                            <ModalLogin/>
                        </>
                }
            </div>
        </div>
    )
}

const mapStateToProps = state =>{
    return{
        user:state.user,
    }
}

export default connect(mapStateToProps,{Logout})(Account) ;
